import React from 'react'
import { Card, Table, Menu, Modal, message } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import { DataService } from "services/roadtalls.service";
import NumberFormat from 'react-number-format';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import { DeleteOutlined, NodeExpandOutlined, ExclamationCircleOutlined } from '@ant-design/icons';

const { Title } = Typography;
const { confirm } = Modal;

const Roadtall = ({systemData}) => {

	const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => confirmDelete(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);


	const confirmDelete = (row) => {
		confirm({
		  title: 'Do you Want to delete this roadtall?',
		  icon: <ExclamationCircleOutlined />,
		  onOk() {
			remove(row._id);
		  }
		});
	};

	const remove = key => {
		DataService.remove(key)
		message.success(`Road tall info deleted`);
	};

  	const { allDestinationsLoading, allDestinations,  allTrucks, allBordersLoading, allBorders, allCurrencyLoading, allCurrency, allOrders, allRentedTrucks, roadTall, roadTallLoading} = systemData;

	const tableColumns = [
		{
			title: 'Date',
			dataIndex: 'id',
			render: (_,record) => new Date(+record.data()._id).toLocaleString()
		},
		{
			title: 'Truck',
			dataIndex: 'id',
			render: (_,record) => record.data().internal ? 
			`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
			:
			`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`
		},
		{
			title: 'Border',
			dataIndex: 'order',
			render: (_,record) => `${allBorders[record.data().border]?.name} - ${allBorders[record.data().border]?.country}`
		},
		{
			title: 'Amount',
			dataIndex: 'amount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().amount} 
							  suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Rate',
			dataIndex: 'exchangeRate',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().exchangeRate} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Total RWF',
			dataIndex: 'totalAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().totalAmount} 
							  suffix={' RWF'} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Departure',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[allOrders[record.data().orderId]?.departure]?.name +' - '+allDestinations[allOrders[record.data().orderId]?.departure]?.country}</Flex>
			),
		},
		{
			title: 'Destination',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[allOrders[record.data().orderId]?.destination]?.name +' - '+allDestinations[allOrders[record.data().orderId]?.destination]?.country}</Flex>
			),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
	];

	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Latest Road tall</Title>
					</div>
				</Flex>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!roadTallLoading ? roadTall.docs.reverse() : []} 
					loading={roadTallLoading}
					rowKey='_id' 
				/>
			</div>
		</Card>
	)
}

export default Roadtall
